.navigation__title {
  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin: 0 0 20px 0;

  @include vp-767 {
    margin: 0;
    padding: 20px 40px 25px 0;
  }
}

.navigation__list {
  // margin: 0;
  // padding: 0;
  list-style: none;
  // column-count: auto;
  // column-width: 220px;
  column-gap: 30px;
  display: grid;
  grid-template-columns: repeat(2, 220px);
  column-gap: 30px;

  @include vp-1023 {
    grid-template-columns: 220px;
  }

  @include vp-767 {
    flex-direction: column;
    flex-wrap: wrap;
    padding-bottom: 10px;
  }
}

.navigation__part-first,
.navigation__part-second {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.navigation__item {
  margin-bottom: 10px;
  margin-left: -40px;
}

.navigation__link {
  color: transparentize($color-default-white, 0.3);

  &:hover {
    color: $color-default-white;
  }
}
